import type {
  MoSerializationPlugin, MoServerSerializationPlugin,
} from '@ali/4ever-cangjie';

import htmlToMarkdown from './htmlToMarkdown';
import { createLeaf } from './leaf';

export default function createSerializer(configs): MoSerializationPlugin {
  return {
    htmlToMarkdown,
    leaf: createLeaf(configs),
  };
}

export function createServerSerializationPlugin(configs): MoServerSerializationPlugin {
  return {
    leaf: createLeaf(configs),
  };
}
